@use 'base/variables' as v;
@use 'base/mixins' as m;
.header{
    padding: 2rem 0;
    

    .contenido{
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
   
        .menu{
            display: flex;
            align-items: center;
            

            i{
                position:absolute;
                right: 3%;
                font-size: 2.7rem;

                @include m.tablet{
                    display: none;
                }
                
            }
        }
            .logo{
                font-weight: 400;
                padding: 1rem;
            }
        @include m.tablet{
        flex-direction: row;
        justify-content: space-between;

            .logo{
                padding: unset;
                font-size: 3rem;

                @include m.desktop{
                    font-size: 3.7rem;
                }   
            }
            

        }

   
    }

    .navegacion{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;

    display: none;
    

                @include m.tablet{
                display: flex;
                flex-direction: row;
                gap:1.2rem;
               
                    
                }
            
                a:hover{
                    opacity: .7;
                }
            }
    .active{
        display: flex;
    }

  
}


